/* Explore Page Responsive Styles */

.explore-filters-sidebar {
  display: none;
  padding-left: 16px !important;
}

.explore-mobile-filters {
  display: block;
}

.explore-mobile-tabs {
  display: flex;
}

.explore-desktop-tabs {
  display: none;
}

.explore-sort-mobile {
  display: block;
  width: 60%;
}

.explore-sort-desktop {
  display: none;
}

/* Media Queries */

/* Small devices (640px and above) */
@media (min-width: 640px) {
  .explore-mobile-filters .MuiPaper-root {
    width: 40% !important;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .explore-mobile-filters .MuiPaper-root {
    width: 30% !important;
  }
}

/* Mobile devices (up to 1023px) */
@media (max-width: 1023px) {
  .explore-filters-sidebar {
    display: none;
  }

  .explore-mobile-filters {
    display: block;
  }

  .explore-mobile-tabs {
    display: flex;
  }

  .explore-desktop-tabs {
    display: none;
  }

  .explore-sort-mobile {
    display: block;
  }

  .explore-sort-desktop {
    display: none;
  }

  .explore-drawer-mobile {
    display: block;
  }

  .explore-drawer-desktop {
    display: none;
  }
}

/* Large Desktop (1024px and above) */
@media (min-width: 1024px) {
  .explore-filters-sidebar {
    display: block;
  }

  .explore-mobile-filters {
    display: none;
  }

  .explore-mobile-tabs {
    display: none;
  }

  .explore-desktop-tabs {
    display: flex;
  }

  .explore-sort-mobile {
    display: none;
  }

  .explore-sort-desktop {
    display: block;
  }
}

